<template>
  <div class="container">
    <h1>Hello {{ user.firstName }} {{ user.lastName }}</h1>
    <div>
      <!-- img-src="https://picsum.photos/600/300/?image=25"
      img-alt="Image"
      img-top -->
      <b-card v-for="req in requests" :key="req.id"
            :title="req.text"
            class="mb-2">
        <!-- <p class="card-text">

        </p> -->
        <b-button href="#/chat" variant="primary">Start Helping</b-button>
      </b-card>
    </div>
  </div>
</template>

<script>
import { mapState, mapGetters, mapActions } from 'vuex'

export default {

  beforeRouteEnter(to, from, next) {
    next(vm => {
      if (!vm.user.username) {
        next({ name: 'Login' })
      }
    })
  },

  data () {
    return {
    }
  },

  mounted() {
    this.helpRequests();
  },

  computed: {
    ...mapState(['user', 'requests']),
    ...mapGetters(['isLoggedIn'])
  },

  methods: {
    ...mapActions(['helpRequests'])
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
</style>



